import React from "react";
import { useParams } from "react-router-dom";
import Container from "../../common/container";
import IconButton from "@material-ui/core/IconButton";
import { workInfo } from "../../data/workInfo";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { Link } from "react-router-dom";
import "./style.css";

export default function Index() {
  const [data, setDatas] = React.useState(workInfo);
  //  const [tool, setTool] = React.useState("");

  const { id } = useParams();

  React.useEffect(() => {
    setDatas(() => {
      return data.find((e) => e.id == id);
    });
  }, []);

  return (
    <>
      <section className="preview-header">
        <small className="bread-crum">
          <Link to="/">
            <IconButton
              style={{
                border: "1px solid #e1e1e1",
                borderRadius: "24px",
                padding: "6px",
              }}
            >
              <ArrowBackIcon fontSize="large" />
            </IconButton>
          </Link>
        </small>
        <article className="preview-info-contener">
          <header className="title">{data.comp}</header>
          <small>Work Type: {data.type}</small>
          <br />
          <small>Tools: {data.tool && data.tool.map(item => item).join(', ')}</small>
        </article>
      </section>
      {data.component}
    </>
  );
}
