import React from "react";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import weblink from "../../data/weblink";
import './style.css'

const useStyles = makeStyles({
  btn: {
    margin: "24px ",
  },
  
});

function Htmlwork({ name }) {
  const classes = useStyles();

  let list = weblink.find((item) => item.name == name);




 

  return (
    <>
      {list.img ? (
        <div className="preview-image-cont">
          <img src={list.img} alt={list.name} className="preview-image" />
          <Button
            variant="outlined"
            color="primary"
            href={list.url}
            target="_blank"
            className={classes.btn}
            size="small"
          >
            Live View
          </Button>
        </div>
      ) : (
        <iframe
          src={list.url}
          style={{ height: "calc(100vh - 100px)", width: "100%" }}
          frameborder="0"
        />
      )}
    </>
  );
}

export default Htmlwork;
